import React, { Component, Fragment } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css'
import { Switch, Route, HashRouter } from 'react-router-dom';
import { TutorialPage } from './pages/tutorial.page'
import { PostDetailPage } from './pages/post_detail';
import { Header } from './components/header';
import { CVPage } from './pages/cv.page';
class PageTemplate extends Component {
  render() {
    return (
      <Fragment>
        <Header />
        <div className='container mt-5'>
          <Switch >
            <Route exact={true} path='/' component={TutorialPage} />
            <Route exact={true} path='/cv' component={CVPage} />
            <Route exact={true} path='/posts/:id' component={PostDetailPage} />
          </Switch>
        </div>
      </Fragment>
    )
  }
}
class App extends Component {
  render() {
    return (
      <HashRouter>
        <Route component={PageTemplate} />
      </HashRouter >
    );
  }
}

export default App;
